@font-face {
  font-family: Lexend;
  src: url("Lexend-VariableFont_wght.bc91a6a1.ttf");
}

@font-face {
  font-family: LXGW;
  src: url("LXGWWenKaiTC-Regular.d5f7c6a1.ttf");
}

html, body {
  font-optical-sizing: auto;
  background: #f4f2ef;
  height: 100%;
  font-family: Lexend, sans-serif;
  font-style: normal;
}

main, #app, #app > div {
  height: 100%;
}

* {
  margin: 0;
  padding: 0;
}

.groups {
  background: #fff;
  border-radius: 12px;
  max-width: 344px;
  margin: 0 auto;
  padding: 8px;
  overflow: auto;
}

.groups .sidebar-link {
  display: none;
}

.lhs-sidebar {
  z-index: 5;
  width: 200px;
  display: none;
  position: fixed;
  top: 12px;
  bottom: 12px;
  left: 12px;
  box-shadow: 0 1px 2px #0000001f;
}

.lhs-sidebar.visible, .lhs-sidebar .sidebar-link {
  display: block;
}

.rhs-sidebar {
  z-index: 5;
  width: 200px;
  display: none;
  position: fixed;
  top: 12px;
  bottom: 12px;
  right: 12px;
  box-shadow: 0 1px 2px #0000001f;
}

.rhs-sidebar .sidebar-link, .rhs-sidebar.visible {
  display: block;
}

.group {
  cursor: pointer;
  padding: 8px;
  display: flex;
}

.group .hanzi {
  text-align: center;
  background: #ebeae6;
  flex: 0 0 56px;
  height: 56px;
  margin-right: 12px;
  line-height: 56px;
}

.mode-selector, .order-selector, .practice-selector {
  border-bottom: 1px solid #000;
}

.batch-selector {
  position: absolute;
  top: 0;
  right: 200px;
}

.practice {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: calc(100vh - 72px);
  display: flex;
}

.practice .proceed {
  color: #fff;
  background: #15141a;
  border: none;
  border-radius: 8px;
  width: 100%;
  height: 48px;
  margin-top: 16px;
  font-size: 16px;
  line-height: 48px;
}

.practice .proceed:disabled {
  opacity: .3;
}

.practice .unit {
  text-align: center;
  box-sizing: border-box;
  background: #fff;
  border-radius: 12px;
  width: 100%;
  max-width: 344px;
  margin: 0 auto;
  padding: 16px;
  font-size: 1.5em;
  position: relative;
}

.practice .pinyin-preview {
  margin-top: 32px;
  font-size: 32px;
}

.practice .translation {
  margin-top: 8px;
  font-size: 20px;
}

.practice .preview {
  text-align: center;
  justify-content: center;
  align-items: center;
  height: 224px;
  font-size: 3em;
  display: flex;
}

.practice .preview #hanzi1, .practice .preview #hanzi2, .practice .preview #hanzi3 {
  display: none;
}

.practice .preview.smaller {
  font-size: .7em;
}

.practice .preview.bigger {
  font-family: LXGW, sans-serif;
  font-size: 5em;
}

.practice .preview.bigger #hanzi1, .practice .preview.bigger #hanzi2, .practice .preview.bigger #hanzi3 {
  display: block;
}

.practice .preview.bigger span {
  display: none;
}

.practice .options {
  flex-wrap: wrap;
  justify-content: center;
  display: flex;
}

.practice .option {
  cursor: pointer;
  background: #ebeae6;
  border-radius: 22px;
  margin: 0 4px 8px;
  padding: 8px 12px;
  font-size: 16px;
}

.practice .option.selected {
  background: red;
}

.practice .option.correct {
  background: green;
}

.hidden {
  display: none !important;
}

.header {
  text-align: center;
  padding-bottom: 64px;
}

.header.shrink {
  justify-content: center;
  padding-top: 30px;
  padding-bottom: 30px;
  display: flex;
}

.header.shrink img {
  max-height: 50px;
}

nav {
  padding: 12px;
  display: flex;
}

nav > div {
  cursor: pointer;
  border-radius: 12px;
  flex-grow: 0;
  height: 48px;
  line-height: 48px;
  box-shadow: 0 1px 2px #0000001f;
}

.home-link {
  background: #fff url("home.1e18d523.svg") center no-repeat;
  width: 48px;
  margin-right: 4px;
}

.groups-trigger, .words-trigger {
  background: #fff url("sidebar.66651287.svg") right 16px center no-repeat;
  padding: 0 48px 0 18px;
}

.words-trigger {
  margin-left: auto;
}

.sidebar-link {
  cursor: pointer;
  background: #fff url("sidebar.66651287.svg") center no-repeat;
  width: 48px;
  height: 48px;
}

.speak {
  cursor: pointer;
  background: #ebeae6 url("speaker.a462f130.svg") center no-repeat;
  border: none;
  border-radius: 22px;
  width: 44px;
  height: 44px;
  position: absolute;
  right: 16px;
}
